.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
}

.form-input {
  padding: 0.7rem 1rem;
  border-width: 1px;
  border-radius: 10px;
}

.form-input:invalid {
  border: 1px solid red;
}

.form-btn {
  width: 50%;
  padding: 0.7rem;
}
